    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

button {
    cursor: pointer;
}

body {
    padding: 2em;
    display: grid;
    height: 100vh;
    grid-template-rows: auto auto 1fr;
    font-family: 'NotoSans-Medium';
}

body.sunny {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
}

body.rainy {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-size: cover;
}

body.mist {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-size: cover;
}

body.overcast {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    background-size: cover;
    background-color: rgb(0 0 0 / 0.5);
    background-blend-mode: darken;
    color: white;
}

body.cloudy {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    background-size: cover;
    color: white;
}

body.partially-cloudy {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
    background-size: cover;
}

body.snowy {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
    background-size: cover;
}

div.search-bar {
    justify-self: end;
}

div.temp-buttons-container {
    justify-self: end;
    display: flex;
}

button.celsius,
button.fahrenheit {
    appearance: none;
    border: none;
    padding: 8px 12px;
    font-family: 'NotoSans-SemiBold';
}

button.celsius.selected,
button.fahrenheit.selected {
    background-color: rgb(17, 67, 67);
    color: white;
}

button.celsius {
    border-radius: 4px 0 0 4px;
}

button.fahrenheit {
    border-radius: 0 4px 4px 0;
}

div.weather {
    display: grid;
    justify-self: start;
    grid-template-rows: auto auto auto;
    align-self: center;
    padding-left: 8em;
    margin-top: -5em;
}

div.progress-container {
    grid-column-start: 1;
    grid-column-end: 2;
    display: grid;
    visibility: hidden;
    place-content: center;
    grid-row-start: 1;
    grid-row-end: 4;
    z-index: 1;
    background-color: rgba(228, 228, 228, 0.9);
}

div.weather.loading div.progress-container {
    visibility: visible;
}

div.general-info {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
    margin-bottom: 1em;
}

p.condition {
    font-size: 1rem;
}

p.address {
    line-height: 1;
    font-size: 2.75rem;
    text-transform: uppercase;
    font-family: 'NotoSans-SemiBold';
}

div.more-info {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 3;
    grid-row-end: 4;
    display: grid;
    grid-template-columns: 1fr auto;
    justify-self: start;
    gap: 1em;
}

div.more-info div.details {
    align-self: center;
    border-left: 2px solid rgb(210, 210, 210);
    padding-left: 32px;
    display: grid;
    gap: 12px;
}

p.temp {
    font-size: 7rem;
    font-family: 'NotoSans-Bold';
    padding-right: 32px;
    position: relative;
}

p.temp sup.temp-symbol {
    font-size: 2rem;
    position: absolute;
    right: 16px;
    top: 0;
}

.search-bar form {
    display: grid;
}

input#city {
    font-size: 1.2rem;
    padding: 4px 8px;
    appearance: none;
    border-radius: 6px 0px 0px 6px;
    border: 2px solid grey;
    max-width: 240px;
    font-family: 'NotoSans-Regular';
    grid-column-start: 1;
    grid-column-end: 2;
}

input#city:focus {
    outline: none;
    border: 2px solid black;
    border-radius: 6px 0px 0px 6px;
}

input#city.error {
    border-color: red;
}

button.submit-city {
    border-radius: 0px 6px 6px 0;
    appearance: none;
    background-color: white;
    border: 2px solid grey;
    background-position: center;
    background-repeat: no-repeat;
    border-left: none;
    grid-column-start: 2;
    grid-column-end: 3;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
    padding: 12px;
}

span.error {
    grid-row-start: 2;
    color: red;
    padding-left: 4px;
    padding-top: 4px;
    font-size: 0.75rem;
    grid-row-end: 3;
    visibility: hidden;
}

span.error.visible {
    visibility: visible;
}

button.submit-city:hover {
    background-color: rgb(233, 233, 233);
}

button.submit-city:active {
    background-color: rgb(204, 204, 204);
}
